import { createTheme } from '@mui/material/styles'
import { red } from '@mui/material/colors'


export const palette = {
    white:     '#fff',
    black:     '#000',
    primary:   '#f26b38',
    warning:   '#f58020',
    danger:    '#e84022',
    secondary: '#17c690',
    success:   '#17c690',
    error:     '#e84022',
    yellow:    '#e9d700',
    gray:      '#b2b2b2',
    // border: 'const borderColor = theme.palette.mode === 'light' ? 'rgba(0, 0, 0, 0.23)' : 'rgba(255, 255, 255, 0.23)';',
    border:    'rgba(0, 0, 0, 0.23)',
    sidebarBg: '#5c5c5c',
    lightGrey: '#FAFAFA',

    headerColor:      '#fff',
    headerColorHover: 'rgba(255,255,255,.7)'
}

const lightThemeColors = {
    ...palette,
    warning:   '#f58020',
    danger:    '#e84022',
    secondary: '#17c690',
    success:   '#17c690',
    error:     '#e84022',
    yellow:    '#e9d700',
    gray:      '#b2b2b2',
    border:    '#d8d8d8',

    // components colors
    headerBg: '#24292f'
}

interface Colors {
    white: string,
    black: string,
    primary: string,
    warning: string,
    danger: string,
    secondary: string,
    success: string,
    error: string,
    yellow: string,
    gray: string,
    border: string,

    sidebarBg: string,
    lightGrey: string,

    headerBg: string,
    headerColor: string,
    headerColorHover: string
}

const constants = {
    HEADER_HEIGHT:    48,
    FOOTER_HEIGHT:    30,
    NAVIGATION_WIDTH: 100
}

const borderRadii = {
    s:  4,
    m:  10,
    l:  25,
    xl: 75
}
const spacings = {
    s:  10,
    m:  14,
    l:  18,
    xl: 24
}

export type TColors = typeof palette | typeof lightThemeColors
export type TConstants = typeof constants
export type TBorderRadii = typeof borderRadii
export type TSpacings = typeof spacings

declare module '@mui/material/styles/createTheme' {
    interface ThemeOptions {
        colors: Colors;
        constants: TConstants;
        borderRadii: TBorderRadii;
        spacings: TSpacings;
    }
    interface Theme {
        colors: Colors;
        constants: TConstants;
        borderRadii: TBorderRadii;
        spacings: TSpacings;
    }

    interface DefaultTheme {
        colors: Colors;
        constants: TConstants;
        borderRadii: TBorderRadii;
        spacings: TSpacings;
    }

    interface DividerProps {
        disablePadding: boolean
    }
}

declare module '@mui/material/Typography' {
    interface TypographyPropsVariantOverrides {
        background: true;
        header: true;
    }
}

export default createTheme({
    colors:     lightThemeColors,
    constants,
    spacings,
    borderRadii,
    typography: {
        fontFamily: 'Roboto Mono'
    },
    components: {
        MuiPaper: {
            defaultProps: {
                elevation: 0
            },
            styleOverrides: {
                root: {
                    zIndex:     2,
                    position:   'relative',
                    overflow:   'hidden',
                    '&::after': {
                        content:            '""',
                        position:           'absolute',
                        top:                '0',
                        left:               '0',
                        width:              '100%',
                        height:             '100%',
                        background:         'linear-gradient(90deg, #11111b 21px, transparent 1%) center, linear-gradient(#11111b 21px, transparent 1%) center, #fff',
                        backgroundSize:     '22px 22px',
                        backgroundPosition: 'center',
                        opacity:            0.2,
                        zIndex:             -1
                    },
                    '& *': {
                        zIndex: 2
                    }
                }
            }
        },
        MuiTextField: {
            defaultProps: {
                variant:   'outlined',
                margin:    'normal',
                size:      'small',
                fullWidth: true
            }
        },
        MuiFormControl: {
            styleOverrides: {
                marginNormal: {
                    marginTop:    0,
                    marginBottom: '16px'
                },
                marginDense: {
                    marginTop:    0,
                    marginBottom: '10px'
                }
            }
        },
        MuiButton: {
            styleOverrides: {
                root: {
                    borderRadius: 0
                },
                contained: {
                    borderRadius: 0,
                    '&:hover':    {
                        backgroundColor: lightThemeColors.primary
                    }
                }
            }
        },
        MuiOutlinedInput: {
            styleOverrides: {
                root: {
                    borderRadius: 0
                }
            }
        },
        MuiAppBar: {
            styleOverrides: {
                root: {
                    background: lightThemeColors.headerBg
                }
            }
        },
        MuiTooltip: {
            defaultProps: {
                disableInteractive: true
            }
        },
        MuiLink: {
            styleOverrides: {
                root: {
                    color:    '#0969da',
                    // fontFamily: 'ui-monospace,SFMono-Regular,SF Mono,Menlo,Consolas,Liberation Mono,monospace',
                    fontSize: '12px'
                }
            },
            defaultProps: {
                underline: 'hover',
                display:   'inline-block'
            },
            variants: [
                {
                    props: {
                        variant: 'background'
                    },
                    style: {
                        background:   '#ddf4ff',
                        padding:      '2px 6px',
                        borderRadius: 6
                    }
                },
                {
                    props: {
                        variant: 'header'
                    },
                    style: {
                        // fontFamily: '-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji"',
                        fontWeight: 600,
                        fontSize:   '14px'
                    }
                }
            ]
        }
    },
    palette: {
        mode:    'dark',
        primary: {
            main: lightThemeColors.primary
        },
        secondary: {
            main: '#19857b'
        },
        error: {
            main: red.A400
        },
        background: {
            default: '#1c1c21',
            paper:   '#28282F'
        },
        text: {
            secondary: '#999'
        }
    }
})